@import url(https://fonts.googleapis.com/css?family=Germania+One|Vollkorn);
body {
  margin: 0;
  padding: 0;
  font-family: 'Vollkorn', cursive;
}

#root {

  padding: 10px;
}

.bar.available {

  opacity: 0;
}

.bar {
  position: relative;
  height: 2px;
  width: 500px;
  margin: 0 auto;
  background: #fff;
  margin-top: 150px;
}

.circle {
  position: absolute;
  top: -30px;
  margin-left: -30px;
  height: 60px;
  width: 60px;
  left: 0;
  background: #fff;
  border-radius: 30%;
  -webkit-animation: move 5s infinite;
}

.bar p {
  position: absolute;
  top: -35px;
  right: -85px;
  text-transform: uppercase;
  color: #347fc3;
  font-family: helvetica, sans-serif;
  font-weight: bold;
}

@-webkit-keyframes move {
  0% {
    left: 0;
  }
  50% {
    left: 100%;
    -webkit-transform: rotate(450deg);
    width: 150px;
    height: 150px;
  }
  75% {
    left: 100%;
    -webkit-transform: rotate(450deg);
    width: 150px;
    height: 150px;
  }
100% {
  right: 100%;
}
}

.ipl-progress-indicator.available {
  opacity: 0;
}
#bar p .available { opacity: 1}


.ipl-progress-indicator {
  background-color: #f5f5f5;
  width: 100%;
  height: 100%;
  position: fixed;
  opacity: 1;
  pointer-events: none;
  transition: opacity cubic-bezier(.4, 0, .2, 1) 436ms;
  z-index: 9999;
}
.insp-logo-frame {
  display: -webkit-flex;
  display: -moz-flex;
  display: flex;
  -webkit-flex-direction: column;
  -moz-flex-direction: column;
  flex-direction: column;
  -webkit-justify-content: center;
  -moz-justify-content: center;
  justify-content: center;
  -webkit-animation: fadein 436ms;
  animation: fadein 436ms;
  height: 98%;
}
.insp-logo-frame-img{
  width: 112px;
  height: 112px;
  -webkit-align-self: center;
  align-self: center;
  border-radius: 50%;
}
.ipl-progress-indicator-head {
  background-color: #c6dafc;
  height: 4px;
  overflow: hidden;
  position: relative;
}
.ipl-progress-indicator .first-indicator, .ipl-progress-indicator .second-indicator {
  background-color: #056D8B;
  bottom: 0;
  left: 0;
  right: 0;
  top: 0;
  position: absolute;
  -webkit-transform-origin: left center;
  transform-origin: left center;
  -webkit-transform: scaleX(0);
  transform: scaleX(0);
}
.ipl-progress-indicator .first-indicator {
  -webkit-animation: first-indicator 2000ms linear infinite;
  animation: first-indicator 2000ms linear infinite;
}
.ipl-progress-indicator .second-indicator {
  -webkit-animation: second-indicator 2000ms linear infinite;
  animation: second-indicator 2000ms linear infinite;
}
.ipl-progress-indicator .insp-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  border-radius: 50%;
  -webkit-align-self: center;
  align-self: center;
}
@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@-webkit-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }

}
@-webkit-keyframes first-indicator {
  0% {
    -webkit-transform: translate(0%) scaleX(0);
            transform: translate(0%) scaleX(0);
  }
  25% {
    -webkit-transform: translate(0%) scaleX(0.5);
            transform: translate(0%) scaleX(0.5);
  }
  50% {
    -webkit-transform: translate(25%) scaleX(0.75);
            transform: translate(25%) scaleX(0.75);
  }
  75% {
    -webkit-transform: translate(100%) scaleX(0);
            transform: translate(100%) scaleX(0);
  }
  100% {
    -webkit-transform: translate(100%) scaleX(0);
            transform: translate(100%) scaleX(0);
  }

}
@keyframes first-indicator {
  0% {
    -webkit-transform: translate(0%) scaleX(0);
            transform: translate(0%) scaleX(0);
  }
  25% {
    -webkit-transform: translate(0%) scaleX(0.5);
            transform: translate(0%) scaleX(0.5);
  }
  50% {
    -webkit-transform: translate(25%) scaleX(0.75);
            transform: translate(25%) scaleX(0.75);
  }
  75% {
    -webkit-transform: translate(100%) scaleX(0);
            transform: translate(100%) scaleX(0);
  }
  100% {
    -webkit-transform: translate(100%) scaleX(0);
            transform: translate(100%) scaleX(0);
  }

}
@-webkit-keyframes second-indicator {
  0% {
    -webkit-transform: translate(0%) scaleX(0);
            transform: translate(0%) scaleX(0);
  }
  60% {
    -webkit-transform: translate(0%) scaleX(0);
            transform: translate(0%) scaleX(0);
  }
  80% {
    -webkit-transform: translate(0%) scaleX(0.6);
            transform: translate(0%) scaleX(0.6);
  }
  100% {
    -webkit-transform: translate(100%) scaleX(0.1);
            transform: translate(100%) scaleX(0.1);
  }
}
@keyframes second-indicator {
  0% {
    -webkit-transform: translate(0%) scaleX(0);
            transform: translate(0%) scaleX(0);
  }
  60% {
    -webkit-transform: translate(0%) scaleX(0);
            transform: translate(0%) scaleX(0);
  }
  80% {
    -webkit-transform: translate(0%) scaleX(0.6);
            transform: translate(0%) scaleX(0.6);
  }
  100% {
    -webkit-transform: translate(100%) scaleX(0.1);
            transform: translate(100%) scaleX(0.1);
  }
}

